.header__link {
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #FFF;
  transition: opacity 1s ease;
  text-decoration: none;
  text-align: right;
}

.header__link:hover {
  opacity: .5;
}

@media screen and (max-width:680px){
  .header__link {
    text-align: center;
  }
}